.headerRow {
  margin-top: 30px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 100px;
}

.todoHeader {
  text-align: center;
  grid-row: 1;
  grid-column: 1 / 3;
  width: 100%;
}

.authPanelContainer {
  grid-row: 1;
  grid-column: 2;
}

.todoHeader > h4 {
  margin-top: 0;
  margin-bottom: 20px;
}

.todo {
  background-color: #2274A5;
  color: white;
  padding-top: 5px;
  padding-left: 5px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 8px;
}

.listContainer {
  padding-left: 15px;
  padding-right: 15px;
}

.noListsPlaceholder {
  font-size: 16px;
  background-color: rgb(243, 243, 243);
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  text-decoration: underline;
  border: none;
  font-family: 'Roboto Slab', Arial, Helvetica, sans-serif;
  cursor: pointer;
  width: 100%;
}

.verifyToSeeDiv {
  font-size: 15px;
  background-color: rgb(243, 243, 243);
  padding: 10px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 10px;
  text-align: center;
  border: none;
  font-family: 'Roboto Slab', Arial, Helvetica, sans-serif;
  margin-top: 30px;
  display: inline-block;
  width: 60%;
}

.verifyToSeeContainer {
  width: 100%;
  text-align: center;
}

.sectionHeader {
  font-size: 25px;
  margin-top: 50px;
  margin-bottom: 20px;
  margin-left: 3px;
  font-weight: 700;
}